import decode from 'jwt-decode';
import axios from 'axios';
import Router from 'vue-router';
const ACCESS_TOKEN_KEY = 'access_token';
const USER_DATA_KEY    = 'user_data';
//const PUBLIC_KEY = 'ssh-rsa  tannermoushey@Tanners-MBP-13';

let router = new Router({
   mode: 'history',
});

export function login(username, password, redirect = true) {

  axios
    .post('/wp-json/studychurch/v1/authenticate', {
      username,
      password
    } )
    .then(response => {
      setAccessToken(response.data.token);
      setUserData(response.data.user);

      if (redirect) {
        router.go('/');
      }
    });
}

export function logout() {
  clearAccessToken();
  router.go('/');
}

export function requireAuth(to, from, next) {
  if (!isLoggedIn()) {
    next({
      path: '/',
      query: { redirect: to.fullPath }
    });
  } else {
    next();
  }
}

export function getAccessToken() {
  return localStorage.getItem(ACCESS_TOKEN_KEY);
}

function clearAccessToken() {
  localStorage.removeItem(ACCESS_TOKEN_KEY);
}

// Get and store access_token in local storage
export function setAccessToken(accessToken) {
  localStorage.setItem(ACCESS_TOKEN_KEY, accessToken);
}

export function getUserData() {
  let data = {};

  try {
    data = JSON.parse(localStorage.getItem(USER_DATA_KEY));
  } catch (e) {
    return data;
  }

  return data;
}

export function getUserMeta(key) {
  let data = getUserData();

  return data[key];
}

export function getUserID() {
  return getUserData().id;
}

export function setUserData(userData) {
  localStorage.setItem(USER_DATA_KEY, JSON.stringify(userData));
}

function clearUserData() {
  localStorage.removeItem(USER_DATA_KEY);
}

export function isLoggedIn() {
  const token = getAccessToken();
  return !!token && !isTokenExpired(token) && getUserID();
}

function getTokenExpirationDate(encodedToken) {
  const token = decode(encodedToken);
  if (!token.exp) { return null; }

  const date = new Date(0);
  date.setUTCSeconds(token.exp);

  return date;
}

function isTokenExpired(token) {
  const expirationDate = getTokenExpirationDate(token);
  return expirationDate < new Date();
}